@import url(https://fonts.googleapis.com/css?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box;overflow-y:overlay}.App{background-color:#121215;min-height:100vh;font-size:1rem;color:white;padding:40px}.logoImg{height:5rem;pointer-events:none;display:inline-block}.logoText{position:relative;padding:1rem 1.2rem;font-size:2rem;font-family:"Bree Serif", serif;letter-spacing:0.1rem;color:#fbff7f;line-height:0;white-space:nowrap}.logoText:before,.logoText:after{content:"";position:relative;left:50%;top:-250%;width:200%;height:200%;background-color:#999}.App-header{width:100%;max-width:900px;margin:0 auto;display:flex}.App-body{width:100%;max-width:900px;margin:20px auto}.App-footer{width:100%;max-width:900px;margin:0 auto;display:block;font-size:0.8em;margin-top:20px;color:#666}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.uiBox{position:absolute;bottom:10px;left:10px;width:200px;height:200px;border:1px dotted #fff}

.spaceContainer{min-height:400px;border:dotted #fbff7f 1px;position:relative;overflow:hidden}.spaceContainer .planetContainer .planetBody{position:absolute;background-color:#3f3;width:90px;height:90px;border-radius:90px;top:calc(50% - 30px);left:calc(50% - 30px)}

.spaceContainer2{min-height:400px;border:dotted #fbff7f 1px;position:relative;overflow:hidden}.spaceContainer2 .planetContainer .planetBody{position:absolute;background-color:#3ff;width:60px;height:60px;border-radius:30px;top:calc(50% - 30px);left:calc(50% - 30px)}.spaceContainer2 .moonContainer{position:absolute;width:100%;height:100%;-webkit-transform-origin:50% 50%;transform-origin:50% 50%}.spaceContainer2 .moonContainer .moonBody{position:absolute;background-color:#ff3;width:30px;height:30px;overflow:hidden;border-radius:30px;top:calc(50% - 15px);left:calc(50% - 15px + 150px);-webkit-transform-origin:50% 50%;transform-origin:50% 50%}.spaceContainer2 .moonContainer .moonBody .moonFace{width:40%;height:40%;position:absolute;top:30%;left:0;background-color:#9b13c1}.spaceContainer2 .uiBox{position:absolute;bottom:10px;left:10px;width:200px;height:200px;border:1px dotted #fff}

